body {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

/* Header  */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black ;
}

.logo-img {
    width: 150px ;
    height: auto ;
}

.nav-items {
    padding-right: 40px ;
}
.nav-items > ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
}
.nav-items > ul > li {
    padding: 10px ;
    margin: 10px ;
    font-size: 18px;
}
.nav-items > ul > li > a {
    color: #000 ;
    text-decoration: none;
}

.login-btn {
    padding: 10px 20px ;
    cursor: pointer ;
    font-size: 16px;
    margin-left: 10px ;
}

/* Body  */

.body-head {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.body {
    padding: 40px ;
}

.search-container {
    margin-left: 60px ;
    margin-top: 10px ;
}
.search-bar {
    border-radius: 10px;
    width: 220px;
    padding: 10px;
    background-color: #9c9797;
    border: none ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}
.search-bar::placeholder {
    color: #000 ;
}
.searchbar-btn {
    cursor: pointer;
    padding: 5px ;
    background-color: #9c9797;
    border-radius: 10px;
    padding: 10px ;
    border: none ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    margin-left: 20px ;
}

.filter {
    margin-left: 60px ;
    margin-top: 10px ;
}
.filter-btn {
    cursor: pointer;
    padding: 5px ;
    background-color: #ab7c7c;
    border-radius: 10px;
    padding: 10px ;
    border: none ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}
.reset {
    margin-left: 30px ;
    margin-top: 10px ;
}
.reset-btn {
    cursor: pointer;
    padding: 5px ;
    background-color: #ab7c7c;
    border-radius: 10px;
    padding: 10px ;
    border: none ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}

.res-container {
    padding: 30px ;
    margin: 20px  ;
    display: flex; 
    flex-wrap: wrap;
}
.res-container > a {
    text-decoration: none ;
}
/* Restaurant Card  */

.res-card {
    width: 250px ;
    height: 500px ;
    padding: 10px ;
    color: #000 ;
    border: 1px solid black ;
    background-color: #9c9797;
    border-radius: 10px ;
    margin-right: 20px ;
    margin-top: 20px ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.res-card:hover {
    cursor: pointer;
}

.res-img {
    width: 100%;
    height: 60%;
    border-radius: 16px;
    overflow: hidden;
    filter: drop-shadow(rgba(0, 0, 0, 0.1) 0px 2px 8px);
}

.res-img > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.res-name {
    font-size: 22px;
    line-height: 24px;
    width : 100% ;
    font-weight: 700;
    color: rgba(2, 6, 12, 0.75) ;
    margin-top: 20px ;
}

.res-starTime {
    font-weight: 600;
    margin-top: 20px ;
}

.res-menu {
    margin-top: 20px ;
    font-size: 18px ;
}



/* Shimmer UI  */


.shimmer-container {
    padding: 46px ;
    margin: 20px  ;
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card {
    width: 250px ;
    height: 350px ;
    background-color: #f0f0f0;
    margin: 20px ;
    border-radius: 10px;
}


.error-div {
    height: 100vh;
    width: 100vw ;
    display: flex;
    justify-content: center;
    align-items: center;
}
.error-div > div {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.error-div > div > h1 {
    font-size: 3rem;
    margin-bottom: 10px ;
}


/* RESTAURANT  */

.restaurant {
    display: flex;
    justify-content: center;
}
.restaurant > div {
    border: 1px solid black ;
    padding-right: 200px ;
    padding-left: 100px ;
    margin: 20px ;
    
}

.restaurant-details {
    padding-left: 40px ;
    margin-top: 50px ;
    border-radius: 10px ;
    padding-bottom: 10px ;
    background: linear-gradient(rgb(255, 255, 255) -6.71%, rgb(235, 235, 242) 56.19%, rgb(223, 223, 231) 106.56%);
}
.restaurant-details ul {
    margin-left: -40px ;
}
.restaurant-details ul > li {
    list-style-type: none;
    margin-top: 5px ;
}

.menu-sub-div {
    border: 1px solid black ;
    border-radius: 10px ;
    padding: 7px ;
    margin: 20px ;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
.menu-sub-div > h2 {
    text-align: center;
}

.menu-element {
    padding: 20px ;
    text-align: left;
    margin: 20px 0 ;
    display: flex;
    justify-content: space-between;
}

.menu-element > div > h4 {
    font-size: 1.5rem ;
}
.menu-element > div > h5 {
    font-size: 16px;
}
.menu-element > div > p {
    font-size: 14px ;
}

.menu-element > img {
    width: 200px ;
}


/* User card  */

.contact-us-h1 {
    text-align: center;
    margin: 50px 0 ;
}

.about-us-h1 {
    text-align: center;
    margin: 50px 0 ;
}

.user-card {
    display: flex;
    justify-content: center;
    align-items: center;
}
.user-card > div {
    border: 1px solid black; 
    padding: 30px ;
    text-align: center;
}
.center-h1 {
    text-align: center;
    margin: 50px ; 
}